<script>
  import Repeats from './Repeats';
  import draft from 'stores/Draft';
  import YarnDropDown from './YarnDropDown';

  $: yarn = -1;
  $: data = [];
  $: length = 1;
  $: if (data[0] !== undefined && data[0].length > 0) {
    length = data[0].length;
  }
  /*
  $: {
    for (let i = 0; i < $draft.yarn.length; i++) {
      let y = $draft.yarn[i];
      data[i] = [y.id, undefined];
    }
  }
  */
  /*
  $: data = [
    [0, 12, undefined, 2, undefined, 2, undefined, 2, 10],
    [
      1,
      undefined,
      10,
      undefined,
      undefined,
      undefined,
      undefined,
      undefined,
      undefined,
    ],
    [
      2,
      undefined,
      undefined,
      undefined,
      10,
      undefined,
      undefined,
      undefined,
      undefined,
    ],
    [
      3,
      undefined,
      undefined,
      undefined,
      undefined,
      undefined,
      10,
      undefined,
      undefined,
    ],
  ];
*/
  $: {
    if (yarn !== -1) {
      data = [...data, [yarn, ...Array(getLength() - 1)]];
      yarn = -1;
    }
  }

  function getLength() {
    return length;
  }

  let repeatComponent;

  function deleteRow(row) {
    data = data.filter((v, i) => i !== row);
  }

  function setValue(column, row, value) {
    for (let i = 0; i < data.length; i++) {
      data[i][column] = undefined;
    }
    data[row][column] = value;
    data = [...data];
  }

  function insertColumn(index) {
    repeatComponent.insertColumn(index - 1);
    data.forEach((row) => row.splice(index, 0, undefined));
    data = [...data];
  }
</script>

<div class="top">
  <table>
    <tbody>
      {#if data.length > 0}
        <tr class="button-row">
          {#each { length: data[0].length } as _, column}
            <td>
              <button
                class="add-button"
                on:click={() => insertColumn(column + 1)}>↓</button
              >
            </td>
          {/each}
        </tr>
      {/if}
      {#each data as _, row}
        <tr>
          <td>
            <YarnDropDown
              selectedYarn={data[row][0]}
              on:input={(e) => {
                if (e.target.value === '-1') {
                  deleteRow(row);
                } else {
                  data[row][0] = e.target.value;
                }
              }}
            />
          </td>
          {#each { length: data[row].length - 1 } as _, column}
            <td>
              <input
                size="4"
                value={data[row][column + 1] !== undefined
                  ? data[row][column + 1]
                  : ''}
                on:input={(e) => setValue(column + 1, row, e.target.value)}
              />
            </td>
          {/each}
        </tr>
      {/each}
      <tr>
        <td>
          <YarnDropDown bind:selectedYarn={yarn} />
        </td>
      </tr>

      <Repeats bind:this={repeatComponent} bind:length />
    </tbody>
  </table>
</div>

<style>
  table {
    border-collapse: collapse;
    border: 3px solid black;
    background-color: white;
    position: relative;
  }

  .top {
    padding-top: 20px;
  }
  td {
    border: 3px solid black;
    min-width: 1.5rem;
    text-align: center;
  }

  tr {
    height: 1rem;
  }

  input {
    margin: 0;
    padding: 0;
    width: 3em;
    background: none;
    border-radius: 0;
  }

  .button-row,
  .button-row td {
    border: 0;
    height: 0;
    white-space: nowrap;
    line-height: 0px;
  }

  .add-button {
    width: 20px;
    height: 20px;
    margin: 0;
    padding: 0;
    float: right;
    transform: translate(calc(50% + 2px), calc(-100% - 4px));
  }
</style>
